import { getProduct } from "../api/page.js";
export const mininss={
    data() {
        return {
          vantList: {
            productid: 1001,
            loading: false,
            finished: false,
            list: [],
          },
          noticeBar: {
            color: "#1989fa",
            background: "#ecf9ff",
            text: "请点击模型CT位置模拟您的测试结果",
          },
        };
      },
      computed: {},
      methods: {
        onLoad() {
          getProduct({ id: this.vantList.productid }).then((response) => {
            this.vantList.list = [];

            response.area=[];
            response.type.map(item=>{
              response.area.push(
                {label:'c'+item,flag:false,},
                {label:'t'+item,flag:false,},
          
            )
              })
            this.vantList.list.push(response);
           
    
            this.vantList.loading = false;
            this.vantList.finished = true;
          });
        },
    
        onClick(item,index,subitem,subindex) {
             this.vantList['list'][index]['area'][subindex]['flag'] =  !this.vantList['list'][index]['area'][subindex]['flag']
         
        },
        goPage(pagepath,pagename,params) {
          this.$router.push({ name: pagename,params:params });
        },
        setclass(item,index,subitem,subindex){

              return['line-'+subitem.label,this.vantList['list'][index]['area'][subindex]['flag']  ? 'checked': ''   ]
                
              
        },
        getPageClass(){
          let temp =this.vantList.productid;
          return ['product-'+temp]
        }
      },
    }

    // export default{mininss}