<template>
  <div class="page">
    <headerNav title="模型结果" />

    <ul>
      <van-list
        v-model="vantList.loading"
        :finished="vantList.finished"
        @load="onLoad"
      >
        <li
          v-for="(item, index) in vantList.list"
          :key="index"
          :class="'couponitem ' + (item.show ? 'show' : '')"
        >
          <div class="item-name">
            <span>{{ item.title }}</span>
          </div>
          <div class="item-coupon">
            <div class="item-img">
              <!-- <img :src="item.model"> -->
              <img src="../assets/images/product-5.png" />
            </div>

            <div class="item-cpninfo">
              <div class="line-box">
                <div
                  class="line line-1-c"
                  @click="onClick(index, subindex, lineindex)"
                ></div>
                <div
                  class="line line-1-t"
                  @click="onClick(index, subindex, lineindex)"
                ></div>


              </div>
            </div>
          </div>
        </li>
      </van-list>
    </ul>

    <van-button type="primary" size="large" @click="goPage('/result')"
      >查看结果</van-button
    >

    <van-notice-bar
      :color="noticeBar.color"
      :background="noticeBar.background"
      >{{ noticeBar.text }}</van-notice-bar
    >
  </div>
</template>

<script>
import { getProduct } from "../api/page.js";

export default {
  name:'component5',
  data() {
    return {
      vantList: {
        productid: 1001,
        loading: false,
        finished: false,
        list: [],
      },
      noticeBar: {
        color: "#1989fa",
        background: "#ecf9ff",
        text: "请点击模型CT位置模拟您的测试结果",
      },
    };
  },
  computed: {},
  methods: {
    onLoad() {
      getProduct({ id: this.vantList.productid }).then((response) => {
        this.vantList.list = [];
        this.vantList.list.push(response);

        this.vantList.loading = false;
        this.vantList.finished = true;
      });
    },

    onClick(index, subindex, lineindex) {
      // console.log(  index,subindex,lineindex )
      let flag =
        this.list[index]["Coupon"][subindex]["line"][lineindex]["flag"];
      this.list[index]["Coupon"][subindex]["line"][lineindex]["flag"] = !flag;
    },
    goPage(pagepath) {
      this.$router.push({ path: pagepath });
    },
  },
};
</script>

