import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const routes = [

  // // {
  // //   path: '/product1',
  // //   component: () => import('../page/product-1'),
  // //   meta: {
  // //     title: '产品1-模拟结果'
  // //   }
  // // },
  // {
  //   path: '/result',
  //   component: () => import('../page/result'),
  //   meta: {
  //     title: '模拟结果'
  //   }
  // },

];

const files = require.context("@/page", true, /\.vue$/);  

files.keys().forEach((key) => { 
  // console.log( key, files(key)  )
  let path = key.substring(key.lastIndexOf('\/') ,key.length-4).replace('-',''),
  name = key.substring(key.lastIndexOf('\/')+1 ,key.length-4).replace('-','');
  routes.push(
  {
    name:name,
    path: path,
    component: files(key).default,
    meta: {
     // title: files(key).default.name || '模拟结果'
    }
  }
    );

});



// console.log( routes  )

// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({ routes });

router.beforeEach((to, from, next) => {
  const title = to.meta && to.meta.title;
  if (title) {
    document.title = title;
  }
  next();
});

export {
  router,routes
};
